<template>
  <!-- v-show="$route.meta.id == 1" -->
  <div class="landing">
    <div class="head">IPark停车管理系统</div>
    <div class="nav">
      <input type="text" placeholder="请输入用户名" v-model="userName" />
      <input type="password" placeholder="请输入密码" v-model="password" />
      <!-- <router-link :to="{name: 'common', query:{userType:1}}">@@</router-link> -->
      <div class="btn">
        <button @click="login" class="common-btn">登录</button>
      </div>
    </div>
    <!-- <div class="foot">待填充</div> -->
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      userName: "",
      password: "",
      userType: 0,
      serverIP: '10.100.34.108'
    };
  },
  mounted(){
    this.getIp()
  },
  methods: {
    getIp(){
      axios({
        method: 'GET',
        url:'http://dragonnode.local/quest?role=mid'
      }).then((res) => {
        this.serverIP = res.data
      })
    },
    login() {
      axios({
        method: "GET",
        url: `http://${this.serverIP}:8080/login/user`,
        headers: { "Content-Type": "application/json;charset=UTF-8" },
        params: {
          username: this.userName,
          password: this.password,
        },
      }).then((res) => {
        // console.log(res);
        if (res.data == "Boss") {
          this.userType = 1;
          this.$router.push({
            name: "common",
            query: { userType: this.userType, serverIP: this.serverIP},
          });
        } else if (res.data == "OrdinaryUser") {
          this.userType = 0;
          this.$router.push({
            name: "common",
            query: { userType: this.userType ,serverIP: this.serverIP},
          });
        } else {
          alert("用户名或密码错误");
        }
      }).catch((err) => {console.log(err,'err')});
    },
  },
};
</script>

